$light-grey: #d9d9d9;


/* Google font */
@import url(https://fonts.googleapis.com/css?family=Lato:400,300);

body {
  font-family                         : 'Lato', sans-serif;
  font-weight                         : 400;
  color                               : #333333;
}
/* View Helpers */
.line-break {
  margin-bottom                       : 1em;
}
.line-break-dbl {
  margin-bottom                       : 2em;
}
.line-break-top {
  margin-top                          :1em;
}
.line-break-dbl-top {
  margin-top                          : 2em;
}
.display-block {
  display                             : block;
}


/********************************************************************
  NAVIGATION
********************************************************************/

/* Font in Top Naviation Bar */
.navbar-nav>li>a {
  font-size                           : 1.2em;
}
/* Top Navigation Bar style overide */
.navbar-custom {
  border-radius                       : 0 !important;
  background-color                    : white;
}
/* To Navigation dropdown menu style overide */
.submenu-icons {
    margin-right                      : 8px;
}
/* Style to "glue" navbar to the top */
.affix {
  top                                 : 0;
  width                               : 100%;
  position                            : fixed;
  z-index                             : 99999;
}
.affix + .container-fluid {
  padding-top                         : 50px;
}


/********************************************************************
  TOP 
********************************************************************/

#top-content {
  padding-top                         : 10px;
  height                              : 130px;  
}
.logo {
  height                              : 100px;
}


/********************************************************************
  MAIN CONTENT
********************************************************************/

#main-content {
  margin-top                          : 50px;
  margin-bottom                       : 150px;
}
/* custom red hyperlink */
.red-link, .red-link:hover, .red-link:active, .red-link:visited {
  color                               : red;
  text-decoration                     : none;
}


/********************************************************************
  CONTACT
********************************************************************/


/********************************************************************
    SIGNUP
********************************************************************/

.signup {
  background-color                    : $light-grey;
  min-height                          : 250px;
  padding-top                         : 30px;
}

.btn-signup:hover, .btn-signup:active {
    background-color: white !important;
    
}
/********************************************************************
    FOOTER 
********************************************************************/

footer {
  background-color                    : #262626;
  min-height                          : 150px;
  color                               : #ccc;
  padding-top                         : 30px;
  padding-bottom                      : 30px; 
}

.footer-credits {
  padding-top                         : 20px;
}

/* hyperlinks in footer */
.footer-color, .footer-color:active, .footer-color:visited, .footer-color:hover, footer-color:link {
    color                               : #ccc;
    text-decoration                     : none;
}

.footer-color:hover {
    color: #fff;
}




